h1{
    font-weight: normal;
}
.PopUpAsso
{
    z-index: 10;
    background-color: white;
    position: fixed;
    border-radius: 40px;
    box-shadow: 0 0 1vmax #858585;
}

.PopUp_BTN{
    cursor: pointer;
}

.PopUp_BTN:hover .PopUpBTNReseaux_Wave{
    transform: scale(1.1);
}

.PopUpBTNReseaux_Wave {
    transition: transform 0.5s;
}

.close_area
{
    cursor: pointer;
    position: fixed;
    top:1.5vh;
    right:4vh;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0%, 0%, 0%, 0.25);
    z-index: 5;
}

#closeCrossPopUpAsso
{
    cursor: pointer;
    grid-column: 3/3;
}

#PoleAssociatif
{
    font-family: Briberra,cursive;
    letter-spacing:2px;
    color:white;
    grid-column: 1/1;
    text-align: left;

}

#EntityName
{
    font-family: Briberra, cursive;
    color:black;
    letter-spacing:2px;
    font-weight: normal;
}

#row1-container
{
    display: grid;
    align-items: center;
}

#row2-container
{
    display: grid;
    align-items: center;
}

#SlideshowDiv
{
    padding: 1vmax;
    max-width: 100%;
}

#Bordure
{
    display: grid;
    border-radius:40px 40px 0 0;
    align-items: center;
}

#LinksPopUp{
    display: flex;
    justify-items: center;
    justify-content: space-evenly;
}

#LeftArrowButtonPopUP{
    cursor: pointer;
    transform: scale(1);
    transition-property: transform;
    transition-duration: 1000ms;
}

#LeftArrowButtonPopUP:hover {
    transform: scale(1.3);
}

#RightArrowButtonPopUP{
    cursor: pointer;
    transform: scale(1);
    transition-property: transform;
    transition-duration: 1000ms;
}

#RightArrowButtonPopUP:hover{
    transform: scale(1.3);
}

#pageNavigatorDiv{
    display: grid;
    grid-template-columns: 10vw auto 10vw;
    grid-gap: 5vw;
    justify-items: center;
    align-items: center;
}

#pageProgessionHexagones{
    display: flex;
    justify-content: space-around;
}

#Texte_PopUp{
    font-family: "IBM_Light", sans-serif;
    color:black;
    text-align: left;
}

@media only screen and (min-width: 768px){
    .pageProgressionHexagones{
        height: 5vh;
    }

    .LinkIconImg{
        height: 10vh;
        padding: 1.5vh;
    }

    .PopUpAsso
    {
        height: 93vh;
        width: 88vw;
        top : 3.5vh;
        left : 5vw;
    }

    #Bordure
    {
        grid-template-columns: 20vw auto 9vh;
        grid-template-rows: 8vh;
        width: 100%;
        height: 8vh;
    }

    #closeCrossPopUpAsso
    {
        height: 2vmax;
    }

    #row1-container
    {
        height: 15vh;
        grid-template-columns: 10vw 30vw 40vw;
        grid-template-rows: 15vh;
        grid-gap: 0.5vw;
        width: 80vw;
        margin: auto;
        justify-items: center;
        padding: 2.3vh;
    }

    #LeftArrowButtonPopUP{
        height: 10vh;
        max-width: 10vw;
    }

    #RightArrowButtonPopUP{
        height: 10vh;
        max-width: 10vw;
    }

    #row2-container
    {
        height: 50vh;
        grid-template-columns: 39.75vw 39.75vw;
        grid-template-rows: 50vh;
        grid-gap: 1vw;
        width: 80vw;
        margin: auto;
        padding: 1vh;
        justify-items: center;
    }

    #PoleAssociatif
    {
        font-size : 2vw;
    }

    #EntityName
    {
        font-size : 5.5vw;
        justify-self: left;
        margin-left: 2vw;

    }

    #EntityLogoPopUp
    {
        height: 15vh;
    }

    #Texte_PopUp
    {
        font-size: 1.4vmax;
        max-width: 50vw;
        margin: 1vw;
    }

    .PopUpBTN
    {
        height: 5vw;
    }
}

@media only screen and (max-width: 767px){
    #LinksPopUp{
        margin-top: 2vh;
    }
    .pageProgressionHexagones{
        height: 4vh;
    }

    .LinkIconImg{
        height: 7vh;
    }

    .PopUpAsso
    {
        height: 98vh;
        width: 95vw;
        top : 1vh;
        left : 2.5vw;
    }

    .PopUpBTN
    {
        height: 16vw;
    }

    #PoleAssociatif
    {
        font-size : 3.6vh;
        justify-content: left;
    }

    #EntityName
    {
        font-size :6vh;
        justify-self: left;
        line-height:40px;
        margin:0;

    }

    #EntityLogoPopUp
    {
        height: 10vh;
    }

    #LeftArrowButtonPopUP{
        width: 4vh;
    }

    #RightArrowButtonPopUP{
        width: 4vh;
    }

    #Bordure
    {
        grid-template-columns: 60vw auto 9vh;
        grid-template-rows: 7vh;
        width: 100%;
        height: 7vh;
    }

    #row1-container
    {
        max-height: 14vh;
        height: auto;
        grid-template-areas:
                "a a"
                "c b";
        grid-gap: 2vh;
        padding-top: 3vh;
        padding-bottom: 3vh;
        justify-items: center;
        margin:auto;

    }

    #pageNavigatorDiv{
        grid-area: a;
        margin:auto;
    }

    #EntityLogoPopUp{
        grid-area: c;
    }

    #EntityName{
        grid-area: b;
    }


    #row2-container
    {
        max-height: 67vh;
        grid-template-rows: auto 30vh;
        grid-template-columns: 80vw;
        grid-gap: 1vh;
        width: 80vw;
        margin: auto;
        padding-bottom: 2vh;
        justify-items: center;
        align-items: center;
    }

    #closeCrossPopUpAsso
    {
        height: 2.7vh;
        margin-left: 5vw;
    }

    #Texte_PopUp
    {
        font-size: 1.4vh;
        width: 100%;
        margin-bottom: 3vh;
    }
}

